// ==========================================================================
// Config
// ==========================================================================

$font-url       : 'https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,600;0,700;0,800;1,600&display=swap';
$font-family    : 'Poppins', sans-serif;
$font-size      : 16px;

$line-height        : 1.42857143;
$letter-spacing     : 0.4px;

$font-weight--regular       : 400;
$font-weight--semi-bold     : 600;
$font-weight--bold          : 700;
$font-weight--extra-bold    : 800;

$brkpoint--xs    : 480px;
$brkpoint--sm    : 768px;
$brkpoint--md    : 992px;
$brkpoint--lg    : 1260px;

$container-width--sm    : 750px;
$container-width--md    : 970px;
$container-width--lg    : 1200px;

$color--white           : white;
$color--license-bg      : #212330;
$color--footer-bg       : #323544;
$color--box-bg          : #333645;

$colors--btns: (
    base: (
        primary     : #333645
    ),
    hover: ()
);

$colors--social-media: (
    base: (),
    hover: (
        facebook    : #3b5998,
        linkedin    : #0077b5,
        twitter     : #00aced,
        instagram   : #d93175
    )
);

$colors--services: (
    base: (
        bespoke     : #67809f,
        it          : #4183d7,
        digital     : #2ecc71,
        telecoms    : #d64541,
        web         : #926fb1,
        cyber       : #f62459,
        developer   : #ce4125,
        software    : #f7b322
    ),
    hover: (
        it          : #2a6ec6,
        digital     : #26ab5f,
        telecoms    : #c42e2a,
        web         : #7e57a0,
        cyber       : #e80a42,
        software    : #e8a008
    )
);